.advantage {
  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: $white;
    text-align: center;
    margin-bottom: 12px;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      padding-left: 160px;
      text-align: left;
      margin-bottom: 14px;
    }

    @media (min-width: $desktop-width) {
      padding: 0;
      text-align: center;
      padding-top: 128px;
    }
  }

  &__text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    color: $white;
    padding: 0 20px;
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
      padding: 0;
      padding-left: 160px;
      text-align: left;
      padding-right: 25px;
    }

    @media (min-width: $desktop-width) {
      text-align: center;
      padding: 0 43px;
    }
  }

  &__wrapper {
    width: 280px;
    margin: 0 auto;
    padding-top: 165px;
    position: relative;

    @media (min-width: $tablet-width) {
      width: 640px;
      padding-top: 36px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      padding-bottom: 41px;
    }
  }

  &__list {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      display: grid;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__item {
    display: none;
    position: relative;

    @media (min-width: $tablet-width) {
      display: block;
      border-bottom: 1px solid rgb(255, 255, 255, 0.1);
      padding-bottom: 37px;
      margin-bottom: 40px;

      &:last-child {
        border-bottom: none;
        margin-bottom: 46px;
        padding-bottom: 0;
      }
    }

    @media (min-width: $desktop-width) {
      border: none;
      border-right: 1px solid rgb(255, 255, 255, 0.1);
      margin: 0;
      padding: 0;

      &:last-child {
        border: none;
        margin: 0;
      }
    }

    &--active {
      display: block;
    }

    &--scissors::before {
      background-image: url("../image/icons/icon__scissors.svg");
    }

    &--armchair::before {
      background-image: url("../image/icons/icon__armchair.svg");
    }

    &--barber-pole::before {
      background-image: url("../image/icons/icon__ barber-pole.svg");
    }
  }

  &__item::before {
    content: "";
    position: absolute;
    top: -125px;
    right: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 95px;
    height: 95px;
    background-repeat: no-repeat;

    @media (min-width: $tablet-width) {
      top: 4px;
      left: 79px;
    }

    @media (min-width: $desktop-width) {
      left: 50%;
    }
  }

  &__wrapper-buttons {
    padding-bottom: 12px;
    display: grid;
    grid-template-columns: repeat(3, min-content);
    justify-content: center;
    gap: 23px;

    @media (min-width: $tablet-width) {
      display: none;
    }
  }

  &__button {
    width: 10px;
    height: 10px;
    background-color: transparent;
    transform: rotate(45deg);
    border: 1px solid $white;

    &--active {
      background-color: $white;
    }
  }

  &__container {
    position: relative;

    &--angle::before {
      background-image: url("../image/icons/angle__mobile.svg");
      background-repeat: no-repeat;
      content: "";
      position: absolute;
      right: 0;
      bottom: -34px;
      left: 0;
      height: 34px;
      transform: rotate(180deg);

      @media (min-width: $tablet-width) {
        background-image: url("../image/icons/angle__tablet.svg");
        bottom: -41px;
        height: 41px;
      }

      @media (min-width: $desktop-width) {
        bottom: -65px;
        height: 65px;
      }
    }
  }
}
