.checkbox-list {
  @media (min-width: $tablet-width) {
    border-top: 1px solid $special-white-2;
  }

  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: $black;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 35px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 39px;
      padding: 0 32px;
      margin-bottom: 62px;
    }
  }

  &__list {
    list-style-type: none;
    margin-bottom: 46px;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      width: 640px;
      margin-bottom: 12px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      grid-template-columns: repeat(3, 1fr);
      margin-bottom: 34px;
    }
  }

  &__lead {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: $black;
    display: grid;
    grid-template-columns: 36px 1fr;
    column-gap: 25px;
    position: relative;
    align-items: center;

    &:hover::before,
    &:hover::after {
      border: 1px solid  $special-brown-7;
    }

    &:active::before,
    &:active::after {
      background-color: $special-brown-7;
    }

    @media (min-width: $tablet-width) {
      font-size: 24px;
      line-height: 31px;
    }

    &::after,
    &::before {
      content: "";
      width: 36px;
      height: 36px;
      grid-column: 1/2;
      grid-row: 1/2;
      background-color: $white;
      border: 1px solid $special-white-2;
    }
  }

  &__label &__input:checked + &__lead::after {
    background-repeat: no-repeat;
    background-position: calc(50% + 0px) 50%;
    background-image: url("../image/icons/icon__checkbox.svg");
    transition: all 0.3s;
  }

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 47px;

      &:nth-child(2) {
        order: 1;
      }

      &:nth-child(4) {
        order: 1;
      }

      &:nth-child(6) {
        order: 2;
      }
    }
  }

  &__input:focus + &__lead::before {
    outline: 4px solid rgba(0, 125, 250, 0.6);
  }
}
