.WorkMonths {
  &__image {
    max-width: 100%;
    height: auto;
    object-fit: contain;
    display: block;

    @media (min-width: $tablet-width) {
      width: auto;
    }
  }

  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    color: $black;
    margin-bottom: 15px;

    @media (min-width: $tablet-width) {
      margin-bottom: 26px;
    }
  }

  &__text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: $black;
    margin-bottom: 15px;

    @media (min-width: $tablet-width) {
      flex-grow: 1;
      margin-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      padding-right: 83px;
    }
  }

  &__content-wrapper {
    border: 1px solid $special-white-2;
    border-top: none;
    padding: 19px 29px 17px 22px;

    @media (min-width: $tablet-width) {
      border: 1px solid $special-white-2;
      border-left: none;
      padding: 44px 60px 31px 50px;
      display: flex;
      flex-direction: column;
    }

    @media (min-width: $desktop-width) {
      padding: 128px 54px 27px 42px;
    }
  }

  &__services-list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 14px;
    column-gap: 35px;

    @media (min-width: $tablet-width) {
      row-gap: 17px;
      column-gap: 7px;
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__services-item {
    position: relative;
    padding-left: 36px;
  }

  &__services-title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: $black;
    text-transform: uppercase;

    &--mustache {
      padding-left: 10px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 45px;
      height: 21px;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      transform: translate(-30%, -50%);
    }

    &--haircut::before {
      background-image: url("../image/icons/scissors-icon.svg");
    }

    &--shaving::before {
      background-image: url("../image/icons/beard-icon.svg");
    }

    &--mustache::before {
      background-image: url("../image/icons/mustache-icon.svg");
    }
  }

  &__image-wrapper {
    position: relative;

    &--banner::before {
      content: " ";
      font-family: "PT Sans Narrow", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 20px;
      line-height: 27px;
      color: $white;
      text-transform: uppercase;
      background-color: $special-brown-1;
      padding: 12px 26px 11px 19px;
      position: absolute;
      bottom: 29px;

      @media (min-width: $tablet-width) {
        bottom: auto;
        top: 35px;
        left: -2px;
      }

      @media (min-width: $desktop-width) {
        top: 26px;
        right: -479px;
        left: auto;
        bottom: auto;
        padding: 12px 19px 11px 19px;
      }
    }

    &--banner::after {
      content: "";
      position: absolute;
      left: 166px;
      bottom: 29px;
      border: 25px solid #826550;
      border-right: 12px solid transparent;
      border-left: none;

      @media (min-width: $tablet-width) {
        bottom: auto;
        top: 35px;
        left: 165px;
      }

      @media (min-width: $desktop-width) {
        top: 26px;
        left: auto;
        right: -319px;
        transform: rotate(180deg);
      }
    }
  }
}
