.modal-login {
  &--open {
    display: block;
  }

  &--close {
    display: none;
  }

  &__wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 135px;
    left: 0;

    @media (min-width: $tablet-width) {
      top: 0;
      position: fixed;
      display: flex;
    }
  }

  &__content {
    width: 320px;
    position: relative;
    margin: auto;
    background-color: $special-white-1;
    box-shadow: 0 30px 50px rgba(0, 1, 1, 0.75);
    font-family: "PT Sans Narrow", sans-serif;
    padding: 37px 20px;

    @media (min-width: $tablet-width) {
      width: 460px;
      padding: 58px 69px 57px 70px;
    }
  }

  &__heading {
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: $black;
    margin-bottom: 20px;
    text-align: center;
    text-transform: uppercase;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 52px;
      margin-bottom: 23px;
    }
  }

  &__text {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: $black;
    text-align: center;
    margin-bottom: 34px;

    @media (min-width: $tablet-width) {
      line-height: 23px;
    }
  }

  &__field,
  &__field-second {
    border: none;
  }

  &__input {
    border: 1px solid $special-white-2;
    font-size: 18px;
    line-height: 30px;
    color: $black;
    font-weight: 400;
    font-family: "PT Sans Narrow", sans-serif;
    padding: 9px 0 9px 19px;
    width: 100%;
    background-repeat: no-repeat;
    background-position: center right 19px;

    @media (min-width: $tablet-width) {
      font-size: 24px;
      line-height: 31px;
      padding: 19px 0 19px 19px;
    }

    &--user {
      background-image: url("../image/icons/icon-user__modal.svg");
    }

    &--password {
      background-image: url("../image/icons/icon-lock__modal.svg");
    }

    &::placeholder {
      color: $black;
    }
  }

  &__group {
    margin-bottom: 6px;

    &:last-child {
      margin-bottom: 26px;
    }
  }

  &__link {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    text-decoration-line: underline;
    color: $black;

    @media (min-width: $tablet-width) {
      text-align: right;
    }
  }

  &__lead {
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: $black;
    display: grid;
    grid-template-columns: 33px 1fr;
    column-gap: 15px;
    align-items: center;

    &::before,
    &::after {
      content: "";
      height: 30px;
      grid-column: 1/2;
      grid-row: 1/2;
      background-color: $white;
      border: 1px solid #e5ddd5;
    }
  }

  &__label-checkbox &__input-checkbox:checked + &__lead::after {
    background-repeat: no-repeat;
    background-position: calc(50% + 1px) 50%;
    background-image: url("../image/icons/icon__checkbox.svg");
    transition: all 0.3s;
  }

  &__button-enter {
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $white;
    background-color: $special-brown-1;
    text-transform: uppercase;
    padding: 14px 0 13px 0;
    text-align: center;
    text-decoration: none;

    & span {
      display: none;
    }

    @media (min-width: $tablet-width) {
      line-height: 23px;
      width: 100%;
      display: block;

      & span {
        display: inline;
      }
    }
  }

  &__button-close {
    font-family: inherit;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $black;
    background-color: transparent;
    border: 1px solid $special-white-2;
    text-transform: uppercase;
    padding: 14px 0 13px 0;

    & span {
      display: inline;
    }

    @media (min-width: $tablet-width) {
      position: absolute;
      top: 62px;
      background-image: url("../image/icons/icon-cross__modal.svg");
      background-repeat: no-repeat;
      width: 26px;
      height: 26px;
      border: none;
      padding: 0;
      right: -93px;

      & span {
        display: none;
      }
    }
  }

  &__wrapper-button {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 20px;

    @media (min-width: $tablet-width) {
      display: block;
    }
  }

  &__field-second {
    display: grid;
    grid-template-columns: 153px 1fr;
    align-items: center;
    column-gap: 23px;
    margin-bottom: 24px;
  }
}
