.stats {
  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
    margin-bottom: 16px;
    padding: 0 27px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 40px;
      text-transform: unset;
      padding: 0;
      margin-bottom: 24px;
      text-align: left;
      padding-right: 45px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 41px;
      padding-right: 0;
    }
  }

  &__text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    color: $black;

    @media (min-width: $tablet-width) {
      text-align: left;
      padding: 5px 60px 0 26px;
    }

    @media (min-width: $desktop-width) {
      padding: 0;
      padding-right: 5px;
      margin-bottom: 34px;
    }
  }

  &__note {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: $white;
    opacity: 0.3;
    text-align: center;
    padding-top: 20px;

    &--hide {
      display: none;

      @media (min-width: $tablet-width) {
        display: block;
        color: $black;
        padding: 0;
        text-align: left;
      }
    }

    &--hides {
      @media (min-width: $tablet-width) {
        display: none;
        padding: 0;
        text-align: left;
      }
    }
  }

  &__header-wrapper {
    width: 320px;
    margin: 0 auto;
    padding: 25px 20px 27px 20px;

    @media (min-width: $tablet-width) {
      width: 640px;
      grid-column: 1/-1;
      background-color: $white;
      display: grid;
      grid-template-columns: 1fr 1fr;
      padding: 25px 17px 30px 34px;
      position: relative;

      &::before {
        content: "";
        position: absolute;
        background-image: url("../image/icons/stast_corner.svg");
        width: 23px;
        height: 23px;
        left: -23px;
        top: 35px;
      }
    }

    @media (min-width: $desktop-width) {
      grid-column: auto;
      grid-template-columns: 1fr;
      width: auto;
      padding: 29px 68px 30px 34px;

      &::before {
        top: 37px;
      }
    }
  }

  &__header {
    background-color: $white;
  }

  &__item-stats {
    padding: 20px 20px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;

    &:nth-child(1) {
      background-color: rgba(45, 38, 130, 0.05);

      @media (min-width: $tablet-width) {
        background-color: rgba(255, 255, 255, 0.05);
      }
    }

    &:nth-child(2n) {
      background-color: rgb(255, 255, 255, 0.12);

      @media (min-width: $tablet-width) {
        background-color: rgb(255, 255, 255, 0.05);
      }
    }

    &:nth-child(2) {
      background-color: #4e4e4e;
    }

    @media (min-width: $tablet-width) {
      grid-template-columns: 1fr;
      row-gap: 12px;
      padding: 23px 20px 35px 34px;
    }
  }

  &__list-stats {
    background-color: $special-black-2;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      width: 640px;
    }
  }

  &__item-heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 40px;
    color: $white;

    @media (min-width: $tablet-width) {
      font-size: 72px;
      line-height: 72px;
    }
  }

  &__item-definition {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 18px;
    color: $white;
    text-align: end;

    @media (min-width: $tablet-width) {
      text-align: left;
      font-size: 20px;
      line-height: 20px;

      br {
        display: none;
      }
    }
  }

  &__wrapper {
    @media (min-width: $tablet-width) {
      width: 640px;
      margin: 0 auto;
      display: grid;
      grid-template-columns: 1fr;
      margin-top: -182px;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(2, 1fr);
      width: 960px;
      padding-bottom: 34px;
      margin-top: -161px;
    }
  }
}
