.radio-list {
  @media (min-width: $tablet-width) {
    border-top: 1px solid $special-white-2;
  }

  &__lead {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: $black;
    text-transform: lowercase;
    display: grid;
    grid-template-columns: 1fr;
    row-gap: 9px;
    position: relative;

    &:hover::before,
    &:hover::after {
      border: 1px solid  $special-brown-7;
    }

    &:active::before,
    &:active::after {
      background-color: $special-brown-7;
    }

    @media (min-width: $tablet-width) {
      font-size: 24px;
      line-height: 31px;
      row-gap: 26px;
    }

    &::before,
    &::after {
      content: "";
      width: 52px;
      height: 52px;
      grid-column: 1/2;
      grid-row: 1/2;
      background-color: $white;
      border: 1px solid $special-white-2;
      border-radius: 50%;

      @media (min-width: $tablet-width) {
        margin: 0 auto;
      }
    }
  }

  &__label &__input:checked + &__lead::after {
    height: 18px;
    width: 18px;
    margin: 18px 0 0 17px;
    background-color: $special-brown-1;
    border: none;
    transition: all 0.3s;

    @media (min-width: $tablet-width) {
      margin: auto;
    }
  }

  &__list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 121px;
    justify-items: center;
    margin-bottom: 161px;

    @media (min-width: $tablet-width) {
      grid-template-columns: repeat(6, 1fr);
      row-gap: 50px;
      align-items: center;
      column-gap: 7px;
      margin-bottom: 187px;
    }

    @media (min-width: $desktop-width) {
      column-gap: 8px;
      padding-left: 24px;
      margin-bottom: 195px;
    }
  }

  &__label {
    position: relative;

    &--admiral::before {
      background-image: url("../image/icons/beard-1.svg");
      width: 52px;
      height: 49px;
      left: 4px;
      top: 97px;
    }

    &--woodcutter::before {
      background-image: url("../image/icons/beard-2.svg");
      width: 58px;
      height: 68px;
      left: -3px;
      top: 97px;
    }

    &--polar::before {
      background-image: url("../image/icons/beard-3.svg");
      width: 63px;
      height: 78px;
      left: -1px;
      top: 97px;
    }

    &--boyar::before {
      background-image: url("../image/icons/beard-4.svg");
      width: 57px;
      height: 97px;
      left: 0;
      top: 98px;
    }

    &--sage::before {
      background-image: url("../image/icons/beard-5.svg");
      width: 45px;
      height: 104px;
      left: 3px;
      top: 98px;
    }

    &::before {
      position: absolute;
      content: "";
      background-repeat: no-repeat;
      background-position: center;

      @media (min-width: $tablet-width) {
        left: 50%;
        transform: translateX(-50%);
        top: 140px;
      }
    }
  }

  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    text-align: center;
    color: $black;
    margin-bottom: 36px;
    text-transform: uppercase;
    padding-left: 3px;

    @media (min-width: $tablet-width) {
      font-size: 30px;
      line-height: 39px;
      padding: 0 27px;
    }
  }

  &__item {
    @media (min-width: $tablet-width) {
      margin-right: 65px;

      &:last-child {
        margin-right: 0;
      }
    }

    @media (min-width: $desktop-width) {
      margin-right: 129px;
    }
  }

  &__input:focus + &__lead::before {
    outline: 4px solid rgba(0, 125, 250, 0.6);
  }
}
