.reviews {
  &__wrapper {
    background-color: $special-white-1;

    @media (min-width: $tablet-width) {
      border-top: 1px solid $special-white-2;
    }
  }

  &__container {
    width: 280px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 640px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      padding-bottom: 71px;
      padding-top: 20px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      padding-top: 19px;
      padding-bottom: 48px;
    }
  }

  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    padding-top: 27px;
    padding-bottom: 28px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 30px;
      text-align: left;
    }

    @media (min-width: $desktop-width) {
      margin-left: 71px;
      padding-top: 25px;
    }
  }

  &__text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: $black;
  }

  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    color: $black;
    margin-bottom: 9px;
  }

  &__list {
    list-style-type: none;
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
      width: 542px;
      margin: 0 auto;
      border: 1px solid $special-white-2;
      padding: 31px 56px 37px 31px;
    }

    @media (min-width: $desktop-width) {
      width: 818px;
      padding: 0;
    }
  }

  &__item {
    display: grid;
    grid-template-columns: max-content 1fr;
    column-gap: 20px;

    @media (min-width: $tablet-width) {
      column-gap: 28px;
    }
  }

  &__image-wrapper {
    justify-self: center;
  }

  &__image {
    max-width: 100%;
    height: auto;
    object-fit: contain;
    display: block;
    margin-top: 11px;

    @media (min-width: $tablet-width) {
      margin-top: 8px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 0;
    }
  }

  &__toggle-slider {
    padding-bottom: 19px;
    display: grid;
    grid-template-columns: repeat(3, min-content);
    justify-content: center;
    gap: 26px;

    @media (min-width: $tablet-width) {
      position: absolute;
      background-color: $special-white-1;
      padding: 0 36px;
      top: 156px;
      left: 50%;
      transform: translateX(-50%);
    }

    @media (min-width: $desktop-width) {
      position: static;
      transform: none;
      margin-top: 42px;
    }
  }

  &__toggle-button {
    width: 7px;
    height: 7px;
    background-color: transparent;
    transform: rotate(45deg);
    border: 1px solid $special-black-2;

    &--current {
      background-color: $special-black-2;
    }
  }

  &__button {
    display: none;

    &:hover {
      border: 1px solid $special-brown-7;
    }

    &:active {
      background-color: $special-brown-7;
      border: 1px solid transparent;
      color: rgba($color: $black, $alpha: 0.3);
    }

    @media (min-width: $tablet-width) {
      display: block;
      grid-column: 2/3;
      font-family: "PT Sans Narrow", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
      color: $black;
      text-decoration: none;
      border: 1px solid $special-white-2;
      width: 160px;
      justify-self: end;
      align-self: center;
      text-transform: uppercase;
      text-align: center;
      padding: 12px 0;
    }
  }

  &__slider-button {
    display: none;

    @media (min-width: $tablet-width) {
      display: flex;
      justify-content: space-between;
      position: absolute;
      top: 50%;
      width: 100%;
      transform: translateY(-50%);
    }

    @media (min-width: $desktop-width) {
      top: 39%;
    }
  }

  &__button-slider {
    @media (min-width: $tablet-width) {
      border: 1px solid $special-white-2;
      background-color: transparent;
      width: 50px;
      height: 160px;
      background-image: url("../image/icons/icon-corner__slider.svg");
      background-position: center;
      background-repeat: no-repeat;

      &--next {
        transform: rotate(180deg);
      }
    }
  }

  &__wrapper-slider {
    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      order: 1;
      position: relative;
      margin-top: 19px;
    }

    @media (min-width: $desktop-width) {
      margin-top: 21px;
    }
  }

  &__content-wrapper {
    @media (min-width: $desktop-width) {
      padding: 32px 50px 0 42px;
    }
  }
}
