.photo-section {
  &__wrapper {
    background-color: $special-white-1;
    padding-top: 27px;
    padding-bottom: 31px;

    @media (min-width: $tablet-width) {
      padding-top: 52px;
      padding-bottom: 70px;
    }

    @media (min-width: $desktop-width) {
      padding-bottom: 90px;
    }
  }

  &__header {
    width: 280px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 640px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 39px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      margin-bottom: 78px;
    }
  }

  &__list {
    list-style-type: none;
    width: 280px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 640px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      gap: 39px;
    }
  }

  &__button {
    display: none;

    &:hover {
      border: 1px solid $special-brown-7;
    }

    &:active {
      background-color: $special-brown-7;
      border: 1px solid transparent;
      color: rgba($color: $black, $alpha: 0.3);
    }

    @media (min-width: $tablet-width) {
      display: block;
      font-family: "PT Sans Narrow", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
      color: $black;
      text-transform: uppercase;
      text-decoration: none;
      border: 1px solid $special-white-2;
      padding: 12px 0 12px 0;
      min-width: 160px;
      text-align: center;
    }
  }

  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    margin-bottom: 31px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 30px;
      margin-bottom: 0;
    }
  }

  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 27px;
    color: $black;
    margin-bottom: 14px;

    @media (min-width: $tablet-width) {
      margin-bottom: 26px;
    }
  }

  &__text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: $black;
    margin-bottom: 20px;

    br {
      display: block;
    }

    @media (min-width: $tablet-width) {
      flex-grow: 1;
    }

    @media (min-width: $desktop-width) {
      br {
        display: block;
      }
    }
  }

  &__services-list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 10px;
    column-gap: 25px;
  }

  &__services-title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 27px;
    color: $black;
    text-transform: uppercase;

    &--mustache {
      padding-left: 10px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      width: 45px;
      height: 21px;
      background-repeat: no-repeat;
      background-position: 50% 50%;
      transform: translate(-30%, -50%);
    }

    &--haircut::before {
      background-image: url("../image/icons/scissors-icon.svg");
    }

    &--shaving::before {
      background-image: url("../image/icons/beard-icon.svg");
    }

    &--mustache::before {
      background-image: url("../image/icons/mustache-icon.svg");
    }
  }

  &__content-wrapper {
    border: 1px solid $special-white-2;
    border-top: none;
    padding: 19px 28px 29px 22px;

    @media (min-width: $tablet-width) {
      border: 1px solid $special-white-2;
      border-left: none;
      padding: 44px 90px 39px 50px;
      display: flex;
      flex-direction: column;
    }

    @media (min-width: $desktop-width) {
      padding: 30px 30px 35px 35px;
      border: 1px solid $special-white-2;
      border-top: none;
      flex-grow: 1;
    }
  }

  &__item {
    margin-bottom: 20px;

    @media (min-width: $tablet-width) {
      display: grid;
      grid-template-columns: 270px 1fr;
      margin-bottom: 41px;
    }

    @media (min-width: $desktop-width) {
      display: flex;
      flex-direction: column;
      margin-bottom: 0;

      &:first-child {
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: max-content 1fr;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__services-item {
    position: relative;
    padding-left: 36px;
  }

  &__image {
    display: block;
    object-fit: contain;
    max-width: 100%;
    height: auto;
  }
}
