.news {
  &__wrapper {
    background-color: $special-white-1;
  }

  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    color: $black;
    padding-top: 68px;
    padding-bottom: 27px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 30px;
      padding-top: 9px;
      text-align: left;
      padding-bottom: 0;
    }

    @media (min-width: $desktop-width) {
      margin-left: 70px;
      grid-column: 1/3;
      text-align: left;
      padding-top: 8px;
    }
  }

  &__text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    color: $black;
    padding-left: 20px;

    @media (min-width: $tablet-width) {
      padding-right: 40px;
      padding-top: 15px;
    }
  }

  &__link {
    text-decoration: none;
    display: flex;
    align-items: center;
  }

  &__link:hover &__text {
    color: $special-brown-1;
    text-decoration: underline;
  }

  &__link:hover &__date {
    background-color: $special-brown-1;
  }

  &__link:hover &__date::after {
    border-top-color: $special-brown-1;
  }

  &__link:active &__text {
    color: $special-brown-6;
    text-decoration: underline;
  }

  &__link:active &__date {
    background-color: $special-brown-6;
  }

  &__link:active &__date::after {
    border-top-color: $special-brown-6;
  }

  &__date {
    background-color: $black;
    padding: 2px 11px 0 10px;
    position: relative;
    margin-bottom: 32px;

    @media (min-width: $tablet-width) {
      margin-bottom: 20px;
    }

    &::after {
      content: "";
      position: absolute;
      right: 0;
      bottom: -10px;
      left: 0;
      width: 0;
      height: 0;
      border: 10px solid transparent;
      border-top-color: #000000;
      border-right-width: 25px;
      border-bottom: none;
      border-left-width: 25px;
    }
  }

  &__day {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: $white;
    text-transform: uppercase;
    margin-bottom: -12px;
    display: block;
    text-align: center;
  }

  &__month {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 30px;
    color: $white;
    text-transform: uppercase;
  }

  &__container {
    width: 280px;
    margin: 0 auto;
    border-bottom: 1px solid $special-white-2;

    @media (min-width: $tablet-width) {
      width: 640px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      border: none;
      padding-top: 83px;
      row-gap: 32px;
      padding-bottom: 34px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      grid-template-columns: repeat(3, 1fr);
      padding-top: 117px;
    }
  }

  &__list {
    list-style-type: none;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      order: 1;
      display: grid;
      grid-template-columns: 1fr 1fr;
      column-gap: 0;
    }

    @media (min-width: $desktop-width) {
      grid-template-columns: repeat(3, 1fr);
      margin-bottom: 5px;
    }
  }

  &__item {
    padding-bottom: 18px;
    border-bottom: 1px solid $special-white-2;
    margin-bottom: 16px;

    @media (min-width: $tablet-width) {
      border: none;
      margin: 0;
      padding: 0;
    }

    @media (min-width: $desktop-width) {
      padding: 0;
      margin: 0;
    }

    &--desktop {
      display: none;

      @media (min-width: $desktop-width) {
        display: block;
      }
    }

    &:last-child,
    &:nth-child(2) {
      border: none;
      margin-bottom: 31px;

      @media (min-width: $tablet-width) {
        margin-bottom: 0;
      }
    }
  }

  &__button {
    text-align: center;
    width: 100%;
    display: block;
    text-decoration: none;
    background-color: transparent;
    border: 1px solid $special-white-2;
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $black;
    text-transform: uppercase;
    padding: 12px 0;
    margin-bottom: 28px;

    &:hover {
      border: 1px solid $special-brown-7;
    }

    &:active {
      background-color: $special-brown-7;
      border: 1px solid transparent;
      color: rgba($color: $black, $alpha: 0.3);
    }

    @media (min-width: $tablet-width) {
      grid-column: 2/3;
      width: 160px;
      margin-bottom: 0;
      justify-self: end;
      align-self: center;
    }

    @media (min-width: $desktop-width) {
      grid-column: 3/4;
    }
  }
}
