.header {
  &__wrapper {
    background-color: $special-black-1;
  }

  &__wrappper-list {
    background-color: $special-brown-1;
    position: relative;
    min-height: 51px;

    @media (min-width: $tablet-width) {
      background-color: $black;
    }
  }

  &__toggle-button {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 70px;
    height: 50px;
    background-color: $special-brown-2;
    border: none;

    @media (min-width: $tablet-width) {
      display: none;
    }

    &:hover,
    &:active {
      background-color: $special-brown-3;
    }
  }

  &__wrappper-list--open:hover &__toggle-button::before,
  &__wrappper-list--open:hover &__toggle-button::after {
    background-color: rgba($color: $white, $alpha: 0.3);
  }

  &__wrappper-list--close:hover &__toggle-button::before {
    background-color: rgba($color: $white, $alpha: 0.3);
    box-shadow: 0 9px 0 0 rgba($color: $white, $alpha: 0.3), 0 18px 0 0 rgba($color: $white, $alpha: 0.3);
  }

  &__wrappper-list--close &__toggle-button::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: $white;
    box-shadow: 0 9px 0 0 $white, 0 18px 0 0 $white;
    transition: all 0.3s;
  }

  &__wrappper-list--open &__toggle-button {
    top: 0;
    right: auto;
    left: 0;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 24px;
      left: 18px;
      width: 36px;
      height: 2px;
      background-color: $white;
    }

    &::before {
      transform: rotate(45deg);
      transition: all 0.3s;
    }

    &::after {
      transform: rotate(-45deg);
      transition: all 0.3s;
    }
  }

  &__nav-link {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
    color: $white;
    text-decoration: none;
    border-top: 1px solid $special-brown-5;
    border-bottom: 1px solid $special-brown-4;
    position: relative;
    display: block;
    padding: 14px 21px;
    padding-bottom: 13px;
    background-color: $special-brown-1;

    &:hover {
      background-color: $special-brown-8;

      @media (min-width: $tablet-width) {
        color: $special-brown-1;
        background-color: transparent;
      }

      @media (min-width: $desktop-width) {
        background-color: $white;
        color: $black;
      }
    }

    &:active {
      color: rgba($color: $white, $alpha: 0.3);
      background-color: $special-brown-8;

      @media (min-width: $tablet-width) {
        background-color: transparent;

        &:active {
          color: rgba($color: $white, $alpha: 0.3);
        }
      }

      @media (min-width: $desktop-width) {
        &:active {
          color: rgba(0, 0, 0, 0.3);
          background-color: $white;
        }
      }
    }

    @media (min-width: $tablet-width) {
      border: none;
      background-color: transparent;
      padding: 0;
      padding-top: 25px;
      padding-bottom: 23px;
    }

    @media (min-width: $desktop-width) {
      padding: 25px 32px 23px;
    }
  }

  &__user-link {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: $white;
    position: relative;
    display: block;
    padding: 13px 21px;
    padding-left: 49px;
    border: none;
    background-color: transparent;
    cursor: pointer;

    @media (min-width: $tablet-width) {
      width: 60px;
      height: 70px;
      padding: 0;
      position: relative;
    }

    @media (min-width: $desktop-width) {
      width: auto;
      height: auto;
      padding: 24px 20px 24px 50px;

      &:hover {
        background-color: $white;
        color: $black;
      }

      &:active {
        color: rgba(0, 0, 0, 0.3);
      }
    }
  }

  &__icon {
    position: absolute;
    top: 17px;
    left: 21px;
    width: 16px;
    height: 16px;
    opacity: 0.3;
    fill: $white;

    @media (min-width: $tablet-width) {
      top: 1px;
      left: -20px;
      right: 0;
      bottom: 0;
      margin: auto;
    }

    @media (min-width: $desktop-width) {
      left: 15px;
      top: 28px;
      right: auto;
      bottom: auto;
    }
  }

  &__user-link:hover &__icon {
    @media (min-width: $tablet-width) {
      fill: $special-brown-1;
      opacity: 1;
    }

    @media (min-width: $desktop-width) {
      fill: rgba(0, 0, 0, 0.3);
    }
  }

  &__user-link:active &__icon {
    @media (min-width: $desktop-width) {
      fill: rgba(0, 0, 0, 0.1);
    }
  }

  &__wrappper-list--close &__nav-item {
    display: none;

    &--current {
      display: block;
    }

    @media (min-width: $tablet-width) {
      display: block;
    }
  }

  &__wrappper-list--close &__user-item {
    display: none;

    @media (min-width: $tablet-width) {
      display: block;
    }
  }

  &__wrappper-list--close &__nav-item--current a {
    text-align: center;
    border: none;
  }

  &__wrappper-list--open &__container-list {
    position: absolute;
    top: 0;
    right: 0;
    left: 70px;
    z-index: 10;
    min-height: 80vh;
    background-color: $special-brown-2;
  }

  &__wrappper-list--open &__nav-list::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    background: linear-gradient(90deg, #000000 0%, rgba(0, 0, 0, 0) 100%);
    opacity: 0.3;
  }

  &__nav-list,
  &__user-list {
    list-style-type: none;
  }

  &__nav-main {
    display: flex;
    flex-direction: column;

    @media (min-width: $tablet-width) {
      flex-direction: column-reverse;
    }
  }

  &__nav-list {
    @media (min-width: $tablet-width) {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      width: 570px;
    }

    @media (min-width: $desktop-width) {
      width: 780px;
    }
  }

  &__user-list {
    @media (min-width: $tablet-width) {
      width: 60px;
    }

    @media (min-width: $desktop-width) {
      max-width: 160px;
      width: auto;
    }
  }

  &__container-list {
    @media (min-width: $tablet-width) {
      display: flex;
      justify-content: space-between;
      width: 768px;
      margin: 0 auto;
      padding: 0 64px;
    }

    @media (min-width: $desktop-width) {
      width: 1200px;
      padding: 0 120px;
    }
  }

  &__nav-item {
    @media (min-width: $tablet-width) {
      margin-right: 34px;
      position: relative;
    }

    &:last-child {
      margin-right: 0;
    }

    @media (min-width: $tablet-width) {
      &--current::before {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        left: 0;
        height: 3px;
        background-color: $special-brown-1;
      }
    }

    @media (min-width: $desktop-width) {
      &--current::before {
        content: "";
        position: absolute;
        right: 0;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        height: 3px;
        background-color: $special-brown-1;
      }
    }

    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }

  &__user-link span {
    @media (min-width: $tablet-width) {
      display: none;
    }

    @media (min-width: $desktop-width) {
      display: inline-block;
      font-weight: 700;
      font-size: 18px;
      line-height: 24px;
      text-transform: uppercase;
    }
  }
}
