.form {
  &__field {
    border: none;
    width: 280px;
    margin: 0 auto;

    @media (min-width: $tablet-width) {
      width: 640px;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }

    @media (min-width: $desktop-width) {
      width: 960px;
    }
  }

  &__button {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: $white;
    text-align: center;
    width: 280px;
    margin: 0 auto;
    display: block;
    border: none;
    background-color: $special-brown-1;
    padding: 14px 0 12px 0;
    text-transform: uppercase;

    &:hover {
      background-color: $special-brown-6;
    }

    &:active {
      color: rgba($color: $white, $alpha: 0.3);
    }

    &:disabled {
      background-color: $special-grey-3;
    }

    @media (min-width: $tablet-width) {
      width: 640px;
      font-size: 24px;
      line-height: 31px;
      padding: 20px 0 19px 0;
      margin-bottom: 37px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      margin-bottom: 48px;
    }
  }
}
