.logo {
  &__imgae {
    margin: 0 auto;
    display: block;
    object-fit: contain;
    max-width: 100%;
    height: auto;

    @media (min-width: $tablet-width) {
      width: 370px;
      height: 100px;
    }

    @media (min-width: $desktop-width) {
      height: 153px;
    }
  }

  &__wrapper {
    padding: 29px 0 26px;

    @media (min-width: $tablet-width) {
      min-height: 400px;
      background-image: url("../image/background/back__tablet.webp");
      background-repeat: no-repeat;
      background-position: center -72px;
      background-size: auto auto;
      padding: 39px 0 0;
    }

    @media (min-width: $desktop-width) {
      background-image: url("../image/background/back__desktop.webp");
      min-height: 648px;
      padding: 54px 0 0;

      @media (min-resolution: 2dppx) {
        background-image: url("../image/background/back__desktop@2x.webp");
        background-size: 2560px auto;
      }
    }


    &--second-container {
      @media (min-width: $tablet-width) {
        min-height: 214px;
        position: relative;
        background-position: center -72px;
        padding-top: 40px;
        background-size: cover;
        padding-left: 11px;
      }

      @media (min-width: $desktop-width) {
        min-height: 310px;
        background-position: center -72px;
        background-size: 2560px auto;
        padding-top: 55px;
        padding-left: 0;
      }
    }

    @media (min-width: $tablet-width) {
      &--second-container::before {
        content: "";
        position: absolute;
        right: 0;
        left: 0;
        bottom: 0;
        height: 42px;
        background-image: url("../image/icons/corner-double.svg");
        background-repeat: no-repeat;
      }
    }

    &--third-container {
      @media (min-width: $tablet-width) {
        padding-left: 0;
        padding-top: 39px;
      }

      @media (min-width: $desktop-width) {
        padding-top: 55px;
      }
    }
  }
}
