.form-section {
  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    color: $black;
    text-transform: uppercase;
    text-align: center;
    padding-top: 30px;
    margin-bottom: 28px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 52px;
      padding: 0;
      margin: 0;
      text-align: left;
    }
  }

  &__button {
    display: none;

    &:hover {
      border: 1px solid $special-brown-7;
    }

    &:active {
      background-color: $special-brown-7;
      border: 1px solid transparent;
      color: rgba($color: $black, $alpha: 0.3);
    }

    @media (min-width: $tablet-width) {
      display: block;
      font-family: "PT Sans Narrow", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 23px;
      color: $black;
      text-decoration: none;
      text-transform: uppercase;
      border: 1px solid $special-white-2;
      width: 160px;
      margin-left: auto;
      text-align: center;
      padding: 13px 0 12px 0;
    }
  }

  &__wrapper {
    background-color: $special-white-1;
    padding-bottom: 39px;
  }

  &__header {
    @media (min-width: $tablet-width) {
      width: 640px;
      margin: 0 auto;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-top: 50px;
      padding-bottom: 67px;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
    }
  }
}
