.footer {
  &__wrapper {
    background-color: $special-black-3;
    position: relative;

    &--angle::before {
      background-image: url("../image/icons/angle-light__mobile.svg");
      background-repeat: no-repeat;
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      left: 0;
      height: 34px;
      transform: rotate(180deg);

      @media (min-width: $tablet-width) {
        content: none;
      }
    }

    &--second-angle::before {
      background-image: url("../image/icons/angle__tablet.svg");
      background-repeat: no-repeat;
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      left: 0;
      height: 34px;
      transform: rotate(180deg);

      @media (min-width: $tablet-width) {
        content: none;
      }
    }
  }

  &__container {
    width: 320px;
    margin: 0 auto;
    padding: 67px 0 24px 0;

    @media (min-width: $tablet-width) {
      width: 640px;
      display: grid;
      grid-template-columns: 1fr 1fr;
      row-gap: 45px;
      padding: 51px 0 24px 0;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      display: flex;
      justify-content: space-between;
    }
  }

  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    color: $white;
    margin-bottom: 10px;

    @media (min-width: $tablet-width) {
      text-align: left;
      line-height: 23px;
      text-transform: uppercase;
      margin-bottom: 23px;
    }
  }

  &__address {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 27px;
    text-align: center;
    color: $white;
    padding: 0 20px;

    @media (min-width: $tablet-width) {
      line-height: 23px;
      padding: 0;
      margin-right: 5px;
    }
  }

  &__telephone {
    display: block;
    text-align: center;
    width: 280px;
    border: 1px solid $special-grey-1;
    padding: 11px 0 10px 0;
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    color: $white;
    text-decoration: none;
    margin: 17px auto 26px auto;

    &:hover {
      border: 1px solid $special-grey-2;
    }

    &:active {
      border: 1px solid $special-black-4;
      background-color: $special-black-4;
      color: rgba($color: $white, $alpha: 0.3);
    }

    @media (min-width: $tablet-width) {
      margin: 0;
      padding: 0;
      text-align: left;
      border: none;
      font-weight: 400;
      line-height: 23px;
      padding-left: 2px;
    }

    & span {
      display: none;

      @media (min-width: $tablet-width) {
        display: inline;
      }
    }
  }

  &__social-heading {
    display: none;

    @media (min-width: $desktop-width) {
      display: block;
      font-family: "PT Sans Narrow", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 18px;
      line-height: 23px;
      text-align: center;
      color: $white;
      margin-bottom: 13px;
    }
  }

  &__social-list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    list-style-type: none;
    margin-bottom: 32px;
  }

  &__item-link {
    display: block;
    width: 106px;
    height: 101px;
    background-position: center;
    background-repeat: no-repeat;
    margin-bottom: 15px;

    &:hover {
      border: 1px solid $special-grey-2;
    }

    &:active {
      border: none;
      background-color: $special-black-4;
      opacity: 0.3;
    }

    @media (min-width: $tablet-width) {
      width: 213px;
    }

    @media (min-width: $desktop-width) {
      width: 50px;
      height: 50px;
    }

    &--VK {
      background-image: url("../image/icons/icon__VK.svg");

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/icon-VK__desktop.svg");
      }
    }

    &--facebook {
      background-image: url("../image/icons/icon__facebook.svg");

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/icon-facebook__desktop.svg");
      }
    }

    &--instagram {
      background-image: url("../image/icons/icon__inst.svg");

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/icon-inst__desktop.svg");
      }
    }
  }

  &__social-item {
    border: 1px solid $special-grey-1;
    width: 106px;
    height: 101px;
    border-right: none;

    &:last-child {
      border-right: 1px solid $special-grey-1;
    }

    &:active {
      border: 1px solid transparent;
    }

    @media (min-width: $tablet-width) {
      width: 213px;
      border-right: none;

      &:last-child {
        border-right: 1px solid $special-grey-1;
      }
    }

    @media (min-width: $desktop-width) {
      width: 50px;
      height: 50px;
    }
  }

  &__copyright-title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    color: $white;
    text-align: center;

    @media (min-width: $tablet-width) {
      padding-bottom: 15px;
      padding-left: 19px;
    }
  }

  &__copyright-link {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 23px;
    color: $white;
    width: 280px;
    margin: 13px auto 0 auto;
    display: block;
    border: 1px solid $special-grey-1;
    padding: 11px 0 14px 0;
    text-decoration: none;
    text-align: center;

    &:hover {
      border: 1px solid $special-grey-2;
    }

    &:active {
      border: 1px solid $special-black-4;
      background-color: $special-black-4;
      color: rgba($color: $white, $alpha: 0.3);
    }

    @media (min-width: $tablet-width) {
      width: 160px;
      margin: 0;
      margin-left: 21px;
    }
  }

  &__social-media {
    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      order: 1;
    }

    @media (min-width: $desktop-width) {
      order: unset;
      margin-left: 85px;
    }
  }

  &__copyright {
    @media (min-width: $tablet-width) {
      width: 180px;
      margin-left: auto;
    }
  }
}
