.contacts {
  &__input {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: $black;
    border: 1px solid $special-white-2;
    padding: 9px 0 9px 18px;
    width: 100%;

    &:hover {
      border: 1px solid  $special-brown-7;
    }

    &:focus {
      outline: 4px solid rgba(0, 125, 250, 0.6);
    }

    &:active {
      border: 1px solid  $special-grey-4;
      outline: none;
    }

    @media (min-width: $tablet-width) {
      font-size: 24px;
      padding: 20px 0 19px 24px;

      & {
        border-right: none;
      }

      &--border {
        border-right: 1px solid $special-white-2;
      }

      &--margin {
        margin-bottom: 35px;

        @media (min-width: $desktop-width) {
          margin-bottom: 0;
        }
      }
    }

    @media (min-width: $desktop-width) {
      & {
        border-right: 1px solid $special-white-2;
      }
    }

    &::placeholder {
      color: $black;
    }
  }

  &__group {
    margin-bottom: 6px;

    &:last-child {
      margin-bottom: 0;
    }

    @media (min-width: $tablet-width) {
      margin-bottom: 0;
    }
  }

  &__full-name {
    margin-bottom: 32px;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      margin-bottom: 35px;
    }

    @media (min-width: $desktop-width) {
      column-gap: 37px;
    }
  }

  &__tel-mail {
    margin-bottom: 32px;

    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
      margin-bottom: 35px;
    }

    @media (min-width: $desktop-width) {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      column-gap: 35px;
    }
  }

  &__additional-information {
    @media (min-width: $tablet-width) {
      grid-column: 1/-1;
    }
  }

  &__textarea {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    color: $black;
    border: 1px solid $special-white-2;
    padding: 9px 0 0 18px;
    width: 100%;
    resize: none;
    height: 112px;
    margin-bottom: 34px;

    @media (min-width: $tablet-width) {
      font-size: 24px;
      line-height: 31px;
      height: 211px;
      padding: 17px 0 0 24px;
      margin-bottom: 46px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 58px;
    }

    &::placeholder {
      color: $black;
    }
  }
}
