.price {
  &__item {
    grid-template-columns: 1fr min-content;
    display: grid;
    margin-bottom: 14px;

    @media (min-width: $tablet-width) {
      grid-template-columns: min-content 248px 160px 1fr;
      counter-increment: count;
      position: relative;
      align-items: center;
      border: 1px solid $special-grey-1;
      border-bottom: 0;
      margin-bottom: 0;

      &:last-child {
        border-bottom: 1px solid $special-grey-1;
      }

      &::before {
        content: counter(count);
        font-family: "PT Sans Narrow", sans-serif;
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 27px;
        color: rgb(255, 255, 255, 0.3);
        padding: 14px 31px 13px 31px;
        border-right: 1px solid $special-grey-1;
      }
    }
  }

  &__list {
    margin-bottom: 32px;

    @media (min-width: $tablet-width) {
      margin-bottom: 9px;
    }

    @media (min-width: $desktop-width) {
      margin-bottom: 0;
    }
  }

  &__definition {
    order: 1;
    grid-column: 1/-1;
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: $white;

    @media (min-width: $tablet-width) {
      border-right: 1px solid $special-grey-1;
      order: 0;
      grid-column: auto;
      padding: 15px 0 14px 19px;

      & span {
        display: none;
      }
    }
  }

  &__cost {
    text-align: end;
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: $white;

    @media (min-width: $tablet-width) {
      text-align: left;
      padding: 15px 0 14px 19px;
    }
  }

  &__heading {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    color: $white;

    @media (min-width: $tablet-width) {
      border-right: 1px solid $special-grey-1;
      padding: 15px 0 14px 19px;
    }
  }

  &__wrapper {
    background-color: $special-black-2;
    position: relative;

    &--angle::before {
      background-image: url("../image/icons/angle-light__mobile.svg");
      background-repeat: no-repeat;
      content: "";
      position: absolute;
      right: 0;
      top: 0;
      left: 0;
      height: 34px;
      transform: rotate(180deg);

      @media (min-width: $tablet-width) {
        height: 34px;
        top: -1px;
      }

      @media (min-width: $desktop-width) {
        content: none;
      }
    }
  }

  &__content {
    width: 280px;
    margin: 0 auto;
    padding: 71px 0 20px 0;

    @media (min-width: $tablet-width) {
      width: 640px;
      padding: 82px 0 52px 0;
    }

    @media (min-width: $desktop-width) {
      width: 960px;
      display: grid;
      grid-template-columns: 630px 270px;
      column-gap: 60px;
      padding: 76px 0 72px 0;
    }
  }

  &__title {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 30px;
    text-align: center;
    color: $white;
    text-transform: uppercase;
    margin-bottom: 22px;

    @media (min-width: $tablet-width) {
      font-size: 40px;
      line-height: 30px;
      text-align: left;
      margin-bottom: 53px;
    }

    @media (min-width: $desktop-width) {
      grid-column: 1/-1;
      margin-bottom: 60px;
    }
  }

  &__promo {
    border: 1px solid $special-grey-1;
    position: relative;

    &--clock::before {
      content: "";
      position: absolute;
      background-image: url("../image/icons/clock__mobile.svg");
      background-repeat: no-repeat;
      width: 58px;
      height: 57px;
      top: 50%;
      transform: translateY(-50%);
      left: 21px;

      @media (min-width: $tablet-width) {
        background-image: url("../image/icons/clock__tablet.svg");
        width: 72px;
        height: 71px;
        left: 20px;
      }

      @media (min-width: $desktop-width) {
        background-image: url("../image/icons/clock__desktop.svg");
        width: 142px;
        height: 139px;
        top: 35px;
        left: 50%;
        transform: translateX(-50%);
      }
    }
  }

  &__promo-text {
    font-family: "PT Sans Narrow", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 27px;
    color: $white;
    padding: 18px 0 17px 112px;

    @media (min-width: $tablet-width) {
      font-weight: 700;
      font-size: 30px;
      line-height: 27px;
      padding: 33px 106px 45px 0;
      text-align: right;
      text-transform: uppercase;
    }

    @media (min-width: $desktop-width) {
      padding: 206px 70px 10px 70px;
      text-align: center;
      text-transform: none;
      font-size: 20px;
      line-height: 27px;
      font-weight: 400;
    }
  }
}
