@charset "UTF-8";
@font-face {
  font-family: "PT Sans Narrow";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTSansNarrow-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "PT Sans Narrow";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/PTSansNarrow-Bold.ttf") format("truetype");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header__wrapper {
  background-color: #242424;
}
.header__wrappper-list {
  background-color: #826550;
  position: relative;
  min-height: 51px;
}
@media (min-width: 768px) {
  .header__wrappper-list {
    background-color: #000000;
  }
}
.header__toggle-button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: #6a4d38;
  border: none;
}
@media (min-width: 768px) {
  .header__toggle-button {
    display: none;
  }
}
.header__toggle-button:hover, .header__toggle-button:active {
  background-color: #624530;
}
.header__wrappper-list--open:hover .header__toggle-button::before, .header__wrappper-list--open:hover .header__toggle-button::after {
  background-color: rgba(255, 255, 255, 0.3);
}
.header__wrappper-list--close:hover .header__toggle-button::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 9px 0 0 rgba(255, 255, 255, 0.3), 0 18px 0 0 rgba(255, 255, 255, 0.3);
}
.header__wrappper-list--close .header__toggle-button::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 9px 0 0 #ffffff, 0 18px 0 0 #ffffff;
  transition: all 0.3s;
}
.header__wrappper-list--open .header__toggle-button {
  top: 0;
  right: auto;
  left: 0;
}
.header__wrappper-list--open .header__toggle-button::before, .header__wrappper-list--open .header__toggle-button::after {
  content: "";
  position: absolute;
  top: 24px;
  left: 18px;
  width: 36px;
  height: 2px;
  background-color: #ffffff;
}
.header__wrappper-list--open .header__toggle-button::before {
  transform: rotate(45deg);
  transition: all 0.3s;
}
.header__wrappper-list--open .header__toggle-button::after {
  transform: rotate(-45deg);
  transition: all 0.3s;
}
.header__nav-link {
