  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
  text-transform: uppercase;
  color: #ffffff;
  text-decoration: none;
  border-top: 1px solid #886e5b;
  border-bottom: 1px solid #71543f;
  position: relative;
  display: block;
  padding: 14px 21px;
  padding-bottom: 13px;
  background-color: #826550;
}
