    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .header__nav-link {
    border: none;
    background-color: transparent;
    padding: 0;
    padding-top: 25px;
    padding-bottom: 23px;
  }
}
@media (min-width: 1200px) {
